.banner {
    position: relative;
    top: 15.5rem;
    display: grid;
    min-height: 55rem;
    font-family: 'electra';
    grid-template-columns: repeat(2, 1fr);
    background: #2E2E33;
    color: white;
    justify-content: space-around;

    @media (max-width: 656px) {
        grid-template-columns: auto;
        grid-template-rows: repeat(2, auto);
    }

    &-left {
        display: grid;
        justify-content: center;
        padding-left: 15rem;
        height: 75%;
        align-self: center;

        &__title {
            font-size: 40px;
        }

        &__tariff {
            font-size: 20px;
        }

        & p {
            font-size: 15px;
        }

        @media (max-width: 656px) {
            padding-left: 20px;
        }

        @media (max-width: 800px) {
            padding-left: 20px;
        }
    }

    &-right {
        background: url(../images/banner__bg.jpg) no-repeat right;
        background-size: contain;
        display: grid;

        @media (max-width: 656px) {
            background: none;
        }

        &__img {
            position: relative;
            top: 50px;
            left: -30px;
            max-width: 600px;
            align-self: flex-end;

            @media (max-width: 1040px) {
                width: 500px;
            }

            @media (max-width: 914px) {
                width: 450px;
            }

            @media (max-width: 854px) {
                width: 400px;
            }

            @media (max-width: 804px) {
                width: 380px;
            }

            @media (max-width: 780px) {
                width: 320px;
            }

            @media (max-width: 656px) {
                justify-self: center;
                top: 0;
                left: 0;
            }

            @media (max-width: 512px) {
                top: 3rem;
                left: 0;
            }
        }
    }
}