.howItWorks {
    font-family: 'electra';
    top: 15.5rem;
    position: relative;
    display: grid;
    grid-template-columns: 45% 40%;
    justify-content: space-around;
    counter-reset: num;
    min-height: 600px;
    align-content: center;
    color: #2e2e33;
    /*background: #FAF9F7;*/
    background-size: 100%;
    background: url(../images/how-it-works-bg.jpg) center bottom no-repeat;

    @media (max-width: 1050px) {
        background-position: -600px 0px;
    }
    
    @media (max-width: 656px) {
        grid-template-columns: 80%;
    }

    &-left {
        display: grid;
        grid-template-rows: 200px 250px;
        max-height: 450px;
        align-self: center;
        width: 70%;
        justify-self: end;

        @media (max-width: 656px) {
            grid-template-rows: 130px 250px;
            justify-self: start;
        }

        &__title {
            font-size: 42px;

            @media (max-width: 656px) {
                font-size: 35px;
            }

            @media (max-width: 376px) {
                font-size: 26px;
            }
        }

        &-item {
            display: grid;
            grid-template-columns: auto 90%;
            &__text {
                font-size: 16px;
                font-weight: 400;
                line-height: 1.3;
            }

            &__num::before {
                display: grid;
                counter-increment: num;
                content: counter(num);
                left: 0;
                font-size: 4rem;
                margin-right: 1rem;
                color: #b81705;
                align-self: center;
            }
        }

        &-container {
            display: grid;
            column-gap: 20px;
            row-gap: 20px;
            grid-template-columns: repeat(2, auto);
            grid-template-rows: repeat(2, auto);

            @media (max-width: 656px) {
                grid-template-columns: auto;
                grid-template-rows: repeat(4, auto);
            }
        }
    }

    &-right {
        &__img {
            position: relative;
            top: 166px;
            left: -30px;

            @media (max-width: 1050px) {
                display: none;
            }
        }
    }
}