.house {
    top: 15.5rem;
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 41%);
    justify-content: center;
    margin-top: 15.5rem;
    margin-bottom: 9.5rem;
    column-gap: 40px;

    @media (max-width: 1050px) {
        margin-top: 0;
    }

    @media (max-width: 656px) {
        grid-template-columns: 80%;
        margin-top: 50px;
        row-gap: 50px;
        grid-template-rows: repeat(2, auto);
    }

    &-text {
        display: grid;
        align-content: center;
        row-gap: 30px;

        @media (max-width: 656px) {
            grid-row: 1/2;
        }

        &__title {
            font-size: 32px;
            font-weight: 700;
        }

        &__text {
            font-size: 18px;
            font-weight: 400;
        }
    }
}