.header {
    width: 100%;
    display: grid;
    height: 15.5rem;
    font-family: 'electra';
    grid-template-rows: 3fr 5fr;
    position: fixed;
    top: 0;
    z-index: 2;

    &-top {
        display: grid;
        font-size: 1.4rem;
        grid-template-columns: 250px;
        background-color: #2f2e33;

        &-container {
            display: grid;
            grid-template-columns: repeat(2, auto);
            align-content: center;
            margin-left: 21px;

            &__account{
                color: #fff;
                position: relative;
                line-height: 2rem;
                padding-left: 21px;
                align-content: center;

                &::before {
                    left: 0;
                    display: block;
                    position: absolute;
                    content: '';
                    width: 3.4rem;
                    height: 2.5rem;
                    background: url(../images/header-top-account__pic.svg) no-repeat;
                }

                &:hover {
                    color: #a3a3a3;
                }
            }

            &__payment{
                color: #fff;
                position: relative;
                line-height: 2rem;
                padding-left: 21px;
                align-content: center;

                &::before {
                    left: 0;
                    display: block;
                    position: absolute;
                    content: '';
                    width: 3.4rem;
                    height: 2.5rem;
                    background: url(../images/header-top-payment__pic.svg) no-repeat;
                }

                &:hover {
                    color: #a3a3a3;
                }
            }
        }
    }

    &-bottom {
        display: grid;
        grid-template-columns: 45.3rem auto;
        background-color: white;

        @media (max-width: 440px) {
            grid-template-columns: 30rem auto;
        }

        &__logo {
            align-self: center;
            display: block;
            width: 29.3rem;
            height: 7.4rem;
            background: url('../images/logo.svg') no-repeat;
            background-size: contain;
            margin-left: 30px;

            @media (max-width: 440px) {
                margin-left: 10px;
            }
        }

        &-list {

            @media (max-width: 1023px) {
                display: none;
            }

            display: grid;
            grid-template-columns: repeat(4, auto);
            align-content: center;

            &__item {
                font-size: 2.3rem;
                color: #2f2e33;
                transition: all 0.2s;
                font-weight: 600;
                text-decoration: none;

                &:hover {
                    color: #005CA9;
                }
            }
        }
    }
}
.nav {
    &-burger {

        @media (max-width: 1023px) {
            display: grid;
        }
        display: none;


        &__img {
            height: 5.7rem;
            justify-self: end;
            align-self: center;
        }

        &-menu {
            grid-template-rows: repeat(4, 2.3rem);
            align-content: space-around;
            border-left: #2f2e33 1px solid;
            border-bottom: #2f2e33 1px solid;
            display: grid;
            padding-left: 10px;
            transition: all 0.3s;
            position: fixed;
            top: 15.5rem;
            right: 0;
            height: 300px;
            width: 200px;
            background-color: white;
            z-index: 15;

            &-hidden {
                right: -200px !important;
            }
        }
    }
}