.numbers {
    position: relative;
    top: 15.5rem;
    margin-top: 50px;

    &-row {
        @media (max-width: 656px) {
            row-gap: 50px;
        }
    }

    &-col {
        display: grid;
        row-gap: 20px;

        &__num {
            font-size: 5rem;
            color: #b81705;
        }

        &__title {
            font-size: 2.5rem;
            color: #b81705;
        }

        &__text {
            font-size: 2rem;
        }
    }
}