.protection {
    font-family: 'electra';
    margin-top: 10rem;
    top: 15.5rem;
    display: grid;
    position: relative;
    grid-template-rows: repeat(2, auto);
    color: #2E2E33;
    row-gap: 70px;
    margin-bottom: 60px;

    &-title {
        text-align: center;
        width: 60%;
        justify-self: center;

        &__title {
            font-size: 35px;
        }

        &__subtitle {
            font-size: 15px;
        }
    }

    &-items {
        justify-self: center;
        width: 80%;
        display: grid;
        grid-template-columns: repeat(3, 27%);
        justify-content: space-between;
        grid-template-rows: repeat(2, auto);
        row-gap: 70px;

        @media (max-width: 656px) {
            grid-template-columns: repeat(6, 60px);
            row-gap: 0;
        }

        @media (max-width: 405px) {
            grid-template-columns: repeat(6, 45px);
        }

        &-hidden {
            grid-column: span 6;

            @media (max-width: 656px) {
                display: grid !important;
            }
        }

        &-item {
            display: grid;
            grid-template-rows: 60px repeat(2, auto);
            row-gap: 23px;

            & img {
                
                @media (max-width: 405px) {
                    height: 45px;
                    width: 45px;
                }
            }

            &__title {
                font-size: 18px;
                font-weight: 700;

                @media (max-width: 656px) {
                    display: none;
                }

                &-hidden {
                    display: grid;
                    
                font-size: 18px;
                font-weight: 700;
                }
            }

            &__text {
                font-size: 15px;

                @media (max-width: 656px) {
                    display: none;
                }

                &-hidden {
                    font-size: 15px;
                    display: grid;
                }
            }
        }
    }
}

.unselected {
    filter: grayscale(1);
}