.policy {
    position: relative;
    top: 15.5rem;

    &-text {
        &-container {
            margin-top: 10px !important;
            margin-bottom: 10px !important;
            font-size: 14px;
            padding-top: 30px;
            padding-left: 20px !important;
            padding-right: 20px !important;
            padding-bottom: 30px;
            -webkit-box-shadow: 1px 1px 13px 5px rgba(34, 60, 80, 0.2);
-moz-box-shadow: 1px 1px 13px 5px rgba(34, 60, 80, 0.2);
box-shadow: 1px 1px 13px 5px rgba(34, 60, 80, 0.2);

            & ul {
                text-align: start;
                & li {
                    padding-bottom: 10px;
                }
            }
        }
    }
}